@mixin respond($breakpoint) {
  $size: map-get($breakpoints, $breakpoint);
  @if ($size) {
    @media #{inspect($size)} {
      // Use inspect() to turn the map value into a string
      @content;
    }
  } @else {
    @error "#{$breakpoint} - error";
  }
}
@mixin isMobile() {
  @media only screen and (hover: none) and (pointer: coarse) {
    @content;
  }
}
